<template>
  <div>
    <div class="money_loading" v-show="isLoading"></div>
    <div v-show="!isLoading">
      <el-row>
        <el-col :span="8">
          <el-select v-model="choice.year" style="width: 100%" @change="changeYear">
            <el-option v-for="item in year" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-col>
        <el-col :span="8">
          <el-select v-model="choice.month" style="width: 100%" @change="changeMonth">
            <el-option v-for="item in month" :key="item.value" :label="item.name" :value="item.value"> </el-option>
          </el-select>
        </el-col>
        <el-col :span="8">
          <el-select v-model="choice.method" style="width: 100%" @change="changeMethod">
            <el-option v-for="item in type" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-col>
      </el-row>
      <div class="pagination_wrapper">
        <div class="sub_wrapper">
          <el-pagination layout="prev, pager, next" :total="totalNumber" :page-size="10" @prev-click="prevClick" @next-click="nextClick" @current-change="currentChange"> </el-pagination>
        </div>
      </div>
      <el-divider></el-divider>
      <div class="list_item_wrapper">
        <list-item v-for="item in shopOrderStatistics.data" :key="item.id" :information="item" :method="shopOrderStatistics.method"></list-item>
      </div>
    </div>
  </div>
</template>

<script>
import ListItem from "@/views/bill/moneyStatement/components/ListItem";
import { mapActions, mapState } from "vuex";
import { startLoading, endLoading } from "@/util/loading.js";
export default {
  mounted() {
    startLoading(".money_loading");
    let obj = {
      ...this.choice,
      shop_id: this.shopId,
    };
    this.getStatisticsOrderInfo({
      obj,
      method: this.choice.method,
    }).then(
      () => {
        this.success();
      },
      () => {
        this.failed();
      }
    );
  },
  data() {
    return {
      isLoading: true,
      choice: {
        year: new Date().getFullYear(),
        month: new Date().getMonth(),
        method: "income",
      },
      month: [
        {
          name: this.$t("month.jan"),
          value: 1,
        },
        {
          name: this.$t("month.feb"),
          value: 2,
        },
        {
          name: this.$t("month.mar"),
          value: 3,
        },
        {
          name: this.$t("month.apr"),
          value: 4,
        },
        {
          name: this.$t("month.may"),
          value: 5,
        },
        {
          name: this.$t("month.june"),
          value: 6,
        },
        {
          name: this.$t("month.july"),
          value: 7,
        },
        {
          name: this.$t("month.aug"),
          value: 8,
        },
        {
          name: this.$t("month.sep"),
          value: 9,
        },
        {
          name: this.$t("month.oct"),
          value: 10,
        },
        {
          name: this.$t("month.nov"),
          value: 11,
        },
        {
          name: this.$t("month.dec"),
          value: 12,
        },
      ],
      type: [
        // {
        //     label: this.$t('bill.all'),
        //     value: 'all',
        // },
        {
          label: this.$t("bill.refund"),
          value: "refund",
        },
        {
          label: this.$t("bill.withdrawal"),
          value: "withdrawal",
        },
        {
          label: this.$t("bill.income"),
          value: "income",
        },
      ],
      year: [
        {
          label: "2020",
          value: 2020,
        },
      ],
    };
  },
  computed: {
    ...mapState({
      shopOrderStatistics: (state) => {
        return state.statistics.shopOrderStatistics;
      },
      shopId: (state) => state.user.data.role_id,
      totalNumber: (state) => state.statistics.totalOrder,
    }),
  },
  methods: {
    success() {
      this.isLoading = false;
      endLoading();
    },
    failed() {
      this.isLoading = false;
      endLoading();
      this.$message.error(this.$t("error.serverErr"));
    },
    changeMethod(value) {
      this.getPanigationData("method", value);
    },
    changeYear(value) {
      this.getPanigationData("year", value);
    },
    changeMonth(value) {
      this.getPanigationData("month", value);
    },
    getAxiosData(method = null, value = null, page = 1) {
      this.isLoading = true;
      startLoading(".money_loading");
      const obj = {
        ...this.choice,
        shop_id: this.shopId,
        [method]: value,
        page: page,
        pageNum: 10,
      };
      this.getStatisticsOrderInfo({
        obj,
        method: this.choice.method,
      }).then(
        () => this.success(),
        () => this.failed()
      );
    },
    getPanigationData(method = null, value = null, page = 1) {
      if (this.choice.method === "withdrawal") {
        // 提款信息
        this.getWithdrawalHistory(page);
      } else {
        this.getAxiosData(method, value, page);
      }
    },
    // 历史提款信息封装
    getWithdrawalHistory(page = 1) {
      this.isLoading = true;
      startLoading(".money_loading");
      let { year, month } = this.choice;
      let obj = {
        year,
        month,
        shop_id: this.shopId,
        success: "1",
        page: page,
        pageNum: 10,
      };
      this.getWithdrawalHistoryData({
        obj,
        method: "withdrawal",
      }).then(
        () => this.success(),
        () => this.failed()
      );
    },

    prevClick(value) {
      this.getPanigationData(null, null, value);
    },
    nextClick(value) {
      this.getPanigationData(null, null, value);
    },
    currentChange(value) {
      this.getPanigationData(null, null, value);
    },
    ...mapActions({
      getWithdrawalInfo: "bill/sGetWithdrawalInfo",
      getStatisticsOrderInfo: "statistics/sGetStatisticsOrderInfo",
      getWithdrawalHistoryData: "statistics/getWithdrawalData",
    }),
  },
  components: {
    ListItem,
  },
};
</script>

<style lang="scss" scoped>
.money_loading {
  height: 200px;
}

.list_item_wrapper {
  height: calc(100vh - 330px);
  overflow-y: auto;
  overflow-x: hidden;
}

.pagination_wrapper {
  position: relative;
  margin-bottom: 2em;
  margin-top: 0.5em;

  .sub_wrapper {
    position: absolute;
    right: 0;
  }
}
</style>
